import React, {
  useState,
  ReactNode,
  useEffect,
  FC
} from 'react';

type sizes = 'xs' | 'sm' | 'md' | 'lg' | 'xl';
type radius = 'none' | 'rounded' | 'pill';
export interface ButtonProps {
  children: ReactNode;
  onClick?: () => void;
  size?: sizes;
  color?: string;
  radius?: radius;
  icon?: ReactNode;
  style?: React.CSSProperties;
}

const Button: FC<ButtonProps> = ({
  children,
  size = 'sm',
  radius = 'none',
  color = 'blue',
  icon,
  onClick,
  style,
}: ButtonProps) => {

  const heightValue =
    size === 'xs'
      ? 30
      : size === 'sm'
        ? 40
        : size === 'md'
          ? 45
          : size === 'lg'
            ? 50
            : 55

  const radiusValue =
    radius === 'none'
      ? 0
      : radius === 'rounded'
        ? '50%'
        : '100%'
  const defaultStyles = {
    border: '1px solid #ccc',
    borderRadius: radiusValue,
    padding: '5px 20px',
    textAlign: 'center',
    wrap: 'nowrap',
    width: 'auto',
    height: heightValue,
    color: 'white',
    backgroundColor: color,
    ...style,
    } as React.CSSProperties;

  return (
    <button
      onClick={onClick}
      style={
        style
          ? style
          : defaultStyles
      }
    >
      {icon}
      {children}
    </button>
  )
}

export default Button;
